
* {
  margin: 0;
  padding: 0;
}
body {
  background: rgba(3, 169, 244, 0.08);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3a50;
  text-align: center;
  margin-top: 60px;
}
.app-title {
  font-size: 35px;
  margin-bottom: 2%;
}
@media screen and (max-width: 750px){
.periodic_table {
      width: 100px;
}
.datas .editor {
      font-size: 25px;
}
}

section {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   padding: 10px;
}
.periodic_table {
   color: #fff !important;
   font-weight: bold;
   font-size: 15px;
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
}
.datas {
   width: 100vw;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   margin-top: 40px;
}
.datas .code-field {
   width: 40%;
   margin-top: 20px;
   margin-left: 8px;
}
.periodic_table + .periodic_table {
   margin-left: 7px;
}
.periodic_table.color-1 {
   background: #00942d;
}
.periodic_table.color-2 {
    background: rgb(0, 206, 188);
}
.periodic_table.color-3 {
    background: rgb(234, 190, 8);
}
.datas .editor {
   max-width: 400px;
   width: 100%;
   min-height: 300px;
   height: 100%;
   border-radius: 3px;
   background: #000;
   color: greenyellow;
   padding: 20px;
   border: 0;
   resize: none;
   overflow: scroll;
   font-size: 14px;
}
.editor:focus {
   outline: 0;
   border: 1px solid greenyellow;
}
@media screen and (max-width: 750px){
.periodic_table {
     width: 100px;
}
.datas .editor {
     max-width: 200px;
     min-height: 200px;
}
}
@media screen and (max-width: 420px) {
.datas {
     -webkit-box-orient: vertical;
     -webkit-box-direction: normal;
         -ms-flex-direction: column;
             flex-direction: column;
}
.datas .code-field {
      width: 100%;
}
}

.download-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-width: 46px;
  max-width: 320px;
  min-height: 46px;
  border-radius: 3px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border: none;
  text-decoration: none;
  color: inherit;
}
